@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes fadeOut {
    0% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}

.fade-out {
    animation: 5s linear 0s fadeOut forwards;
}

.faster-fade-out {
    animation: 3s linear 0s fadeOut forwards;
}

@font-face {
    font-family: 'Beowulf';
    src: url('../fonts/beowulf/beowulf-ot.otf');
}

@font-face {
    font-family: 'Cerne';
    src: url('../fonts/cerne/cerne.otf');
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

:root {
    // values:
    --rvv: calc(1rem + 1vh + 1vw);

    // colors:
    --blue: #287174;
    --darker-green: #2e783e;
    --green: #48a55c;
    --yellow: #fce366;
}


html {
    box-sizing: border-box;
    font-family: 'Novelist', 'Times New Roman', Times, serif;
}